body {
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}
iframe {
  margin: 0 0 20px;
  display: inline-block;
  width:50%;
  border: 0;
  border-left: 15px solid #fff;
  position: relative;
  border-right: 15px solid #fff;
  resize: auto;
  min-height: 50vh;
}
#site-details {
  display: flex;
  flex-wrap: wrap;
}
#page-numbers {
  padding: 30px 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
}
#page-numbers > li {
  padding: 5px;
  display: block;
  margin: 4px;
  cursor: pointer;
  border: 1px solid;
  line-height: 1;
  color: #333;
  font-size: 16px;
  text-transform: uppercase;
  min-width: 30px;
  text-align: center;
}
#page-numbers > li:hover {
  background: #333;
  color: #fff;
}